// color pallette
$brown: #994c3d; // buttons, etc
$dark-green: #2e443a; // headings
$dark: #1b2429; // dark bg
$grey: #767676; // minor elements, breadcrumb links
$grey-beige: #cfc5b9; // headings on dark
$light-grey: #e2ddd6; // bg texture / mtns
$lighter-grey: #ece9e5; // very sparingly used on some bg's
$light: #ffffff; // body bg
$primary: $brown;
$secondary: $grey-beige;
$white: #fff; // just so we have a true white in case $light changes
$black: #000; // same deal here too
$bg-light: #f6f5f3;
// fonts
$body-font: "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
$heading-font: "big-caslon-fb", serif;

// breakpoints
$bp-sm: 580px;
$bp-md: 767px;
$bp-lg: 992px;
$bp-xl: 1199px;
$bp-xxl: 1440px;

// paddings
$padding-sm: 4em;
$padding-md: 6em;
$padding-lg: 9.375em; // the "150px" padding in the .psd's
$padding-xl: 12.5em; // the "200px" padding in the .psd's
// easing timing fns
@import 'lib/easings';

// mixins
@import 'lib/mixins';

